<!-- home -->
<template>
  <div class="app-container">
    <div class="warpper">
      <!--  -->
      <p class="tip">{{tipTxt}}</p>
      <div class="photoBox">
        <video
          @play="onPlay($event)"
          ref="myVideo"
          id="myVideo"
          preload="auto"
          loop
          playsinline
          muted
          autoplay
          webkit-playsinline="true"
        ></video>
        <canvas id="overlay" ref="overlay" />
      </div>
      <div>
        <img :src="imgSrc" alt="" class="imgSrc">
      </div>
    </div>
  </div>
</template>

<script>
// 
import * as faceapi from 'face-api.js'

export default {
    name:'Face',
    data() {
        return {
            tipTxt: '...',
            vid: '',
            vid_width: 200,
            vid_height: 200,
            overlay: '',
            palyFlag: false,
            isOpenMouth: false, // 
            isTwinkle: false, // 
            last_mouth_distance: 0, // 
            imgSrc: '', // 
            mouthNum: 0, // 
            last_nose_distance_y: 0, // 
            last_nose_distance_x: 0, // 
            lastTime: 0, // 
            last_DIF: 0, // 
            first_outline_l: [], // 
            first_outline_r: [], // 
            counter: 0, // ,
            last_nose_left: 0,
            last_nose_top: 0,
            last_dis_eye_norse: 0
        }
    },
    mounted() {
        var that = this;
        this.vid = this.$refs.myVideo;
        this.vid_width = this.$refs.myVideo.width;
        this.vid_height = this.$refs.myVideo.height;
        this.overlay = this.$refs.overlay;
        this.overlayCC = this.$refs.overlay.getContext('2d');
        this.initFun();
    },
    created() {
    },
    methods: {
    // 
        initFun: async function(input) {
            const video = this.vid; const canvas = this.overlay;
            // 
            Promise.all([ //  jsonshard  SyntaxError: Unexpected token < in JSON at position 0
                faceapi.nets.tinyFaceDetector.loadFromUri('/src/assets/models'),
                faceapi.nets.faceLandmark68Net.loadFromUri('/src/assets/models'),
                faceapi.nets.faceRecognitionNet.loadFromUri('/src/assets/models'),
                faceapi.nets.faceExpressionNet.loadFromUri('/src/assets/models')
            ]).then(() => {
                navigator.getUserMedia(
                    { video: {}},
                    stream => {
                        video.srcObject = stream
                        this.palyFlag = true
                    },
                    err => {
                        this.palyFlag = false
                        console.error(err)
                    }
                );
            }); // 
        },
        onPlay: async function(event) {
            var that = this;
            const video = this.vid; const canvas = this.overlay; const vid_width = document.querySelector('#myVideo').videoWidth || 200; const vid_height = document.querySelector('#myVideo').videoHeight || 200;
            if (this.vid.paused || this.vid.ended || !this.palyFlag) {
                return setTimeout(() => this.onPlay())
            }
            const displaySize = { width: vid_width, height: vid_height }; // video
            faceapi.matchDimensions(canvas, displaySize); // 
            const detections = await faceapi
                .detectAllFaces(video, new faceapi.TinyFaceDetectorOptions({ inputSize: 128 }))
                .withFaceLandmarks()
                .withFaceExpressions();
            const resizedDetections = faceapi.resizeResults(
                detections,
                displaySize
            );
            if (resizedDetections.length == 0) {
                this.tipTxt = ''
                // return
            } else {
                // console.log(resizedDetections[0].alignedRect.relativeBox.bottom)
                // 
                if (resizedDetections[0].alignedRect.relativeBox.top > 0.55 || resizedDetections[0].alignedRect.relativeBox.left < 0.2 || resizedDetections[0].alignedRect.relativeBox.right > 0.8 || resizedDetections[0].alignedRect.relativeBox.bottom > 0.98) {
                    this.tipTxt = ''
                } else {
                    this.tipTxt = ''
                    const landmarks = resizedDetections[0].landmarks;
                    const landmarkPositions = landmarks.positions
                    // 
                    // 68(FaceLandmarks68)
                    // const jawOutline = landmarks.getJawOutline()
                    // const nose = landmarks.getNose()
                    // const mouth = landmarks.getMouth()
                    // const leftEye = landmarks.getLeftEye()
                    // const rightEye = landmarks.getRightEye()
                    // const leftEyeBbrow = landmarks.getLeftEyeBrow()
                    // const rightEyeBrow = landmarks.getRightEyeBrow()
                    if (!this.isOpenMouth) {
                        // 
                        this.openMouth(landmarkPositions)
                        // 
                        // this.shakeHead(landmarkPositions)
                        // 
                        // this.twinkle(landmarkPositions)
                    } else if (!this.isTwinkle) {
                        // this.twinkle(landmarkPositions);
                    }
                    faceapi.draw.drawFaceLandmarks(canvas, resizedDetections);
                }
                // faceapi.draw.drawDetections(canvas, resizedDetections);
                // faceapi.draw.drawFaceExpressions(canvas, resizedDetections);
            }
            if (this.isOpenMouth) {
                // 
                this.getPhoto();
            } else {
                setTimeout(() => this.onPlay())
            }
        },
        // 
        openMouth: function(positions) {
            // 63: 67
            var mouth_distance = positions[67].y - positions[63].y;
            var nose_distance_y = positions[31].y; var nose_distance_x = positions[31].x;
            // console.log(Math.abs(nose_distance_x-this.last_nose_distance_x))
            if (Math.abs(nose_distance_y - this.last_nose_distance_y) > 3 && Math.abs(this.last_nose_distance_x - nose_distance_x) > 4) {
                this.tipTxt = ''
            } else {
                this.tipTxt = ''
            }
            this.last_nose_distance_y = nose_distance_y;
            this.last_nose_distance_x = nose_distance_x;
            if (this.last_mouth_distance > 0 && mouth_distance > 0 && (Math.abs(this.last_mouth_distance - mouth_distance) > 6) && (Math.abs(this.last_nose_distance_y - nose_distance_y) < 0.6 && Math.abs(this.last_nose_distance_x - nose_distance_x) < 0.6)) {
                // this.tipTxt=''
                this.mouthNum++;
                if (this.mouthNum > 1) {
                    console.log('')
                    this.tipTxt = '...'
                    this.isOpenMouth = true;
                    this.last_mouth_distance = 0;
                    this.mouthNum = 0;
                }
            }
            this.last_mouth_distance = mouth_distance;
        },
        // 
        getPhoto: function() {
            this.overlayCC.clearRect(0, 0, this.overlay.width, this.overlay.height);
            this.overlayCC.drawImage(this.vid, 0, 0, this.overlay.width, this.overlay.height)
            // let dataUrl = overlay.toDataURL('image/jpeg', 1);
            // imgbase64 = dataUrl.replace(/^data:image\/\w+;base64,/, "");
            var snapData = this.overlay.toDataURL('image/png');
            var imgSrc = 'data:image/png;' + snapData;
            this.imgSrc = imgSrc;
            //   
        },
        // 
        shakeHead: function(positions) {
            if (positions.length == 0) {
                return;
            }
            // 
            if (this.lastTime == 0 || (new Date().getTime() - this.lastTime > 500 && new Date().getTime() - this.lastTime < 10000)) {
                // console.log(positions[62][0])
                // 
                var l_diff_x = positions[31].x - positions[2].x;
                // 
                var l_diff_y = positions[31].y - positions[2].y;
                // 
                var l_distance = Math.pow((l_diff_x * l_diff_x + l_diff_y * l_diff_y), 0.5);
                // 
                var r_diff_x = positions[16].x - positions[31].x;
                // 
                var r_diff_y = positions[16].y - positions[31].y;
                // 
                var r_distance = Math.pow((r_diff_x * r_diff_x + r_diff_y * r_diff_y), 0.5);
                // 
                var lr_diff_x = positions[16].x - positions[2].x;
                // 
                var lr_diff_y = positions[16].y - positions[2].y;
                // 
                var lr_distance = Math.pow((lr_diff_x * lr_diff_x + lr_diff_y * lr_diff_y), 0.5);
                // 
                // l_distance < r_distance;
                // l_distance > r_distance;
                // var DIF = Math.abs( l_distance - r_distance);
                this.first_outline_l.push(l_distance);
                this.first_outline_r.push(r_distance);
                // 
                if ((l_distance > r_distance && Math.abs(l_distance - this.first_outline_l[0]) > 20 && Math.abs(r_distance - this.first_outline_r[0]) > 30) || (l_distance < r_distance && Math.abs(l_distance - this.first_outline_l[0]) > 30 && Math.abs(r_distance - this.first_outline_r[0]) > 50)) {
                    this.counter++
                }
                if (this.counter > 1) {
                    console.log('')
                    this.getPhoto()
                    this.counter = 0;
                    this.first_outline_l = [];
                    this.first_outline_r = [];
                }
                //  
                // this.last_DIF = DIF;
                this.lastTime = new Date().getTime();
            }
        },
        // 
        twinkle: function(positions) {
            // 38,39    4241
            // 44,45    4847
            // 
            // let l_eye_distance_1 =  positions[38].y - positions[42].y;
            // let l_eye_distance_2 =  positions[39].y - positions[41].y;
            // let r_eye_distance_1 =  positions[48].y - positions[44].y;
            // let r_eye_distance_2 =  positions[47].y - positions[45].y;
            // console.log(l_eye_distance_1,l_eye_distance_2,r_eye_distance_1,r_eye_distance_2)
            if (positions.length == 0) {
                return;
            }
            if (this.lastTime == 0 || (new Date().getTime() - this.lastTime > 10)) {
                var xdiff1 = positions[31].x - positions[38].x;
                var ydiff1 = positions[31].y - positions[38].y;
                // 
                var dis_eye_norse1 = Math.pow((xdiff1 * xdiff1 + ydiff1 * ydiff1), 0.5);
                var xdiff2 = positions[31].x - positions[45].x;
                var ydiff2 = positions[31].y - positions[45].y;
                // 
                var dis_eye_norse2 = Math.pow((xdiff2 * xdiff2 + ydiff2 * ydiff2), 0.5);
                // 
                var dis_eye_norse = (dis_eye_norse1 + dis_eye_norse2);
                if (Math.abs(positions[31].x - this.last_nose_left) < 0.5 && Math.abs(positions[31].y - this.last_nose_top) < 0.5) {
                    if ((Math.abs(dis_eye_norse - this.last_dis_eye_norse) > dis_eye_norse * 1 / 60)) {
                        alert('')
                        this.getPhoto()
                        this.last_nose_left = 0;
                        this.last_nose_top = 0;
                        this.last_dis_eye_norse = 0;
                        this.lastTime = 0;
                    }
                }
                this.last_nose_left = positions[31].x;
                this.last_nose_top = positions[31].y;
                this.last_dis_eye_norse = dis_eye_norse;
                this.lastTime = new Date().getTime();
            }
        }
    }
}
</script>
<style lang="scss" scoped>
$unitSize: 1/37.5;
.app-container {
  background: #fff;
  height: 100vh;
  box-sizing: border-box;
  .warpper {
    height: 100%;
    display: flex;
    box-sizing: border-box;
    padding-top: 30px;
    flex-direction: column;
    align-items: center;
    background: rgba(0, 0, 0, 0.6);
    .tip {
      font-size: 18rem * $unitSize;
      color: #fff;
      margin-bottom: 40px;
      height: 20px;
    }
    .photoBox {
      width: 180rem * $unitSize;
      height: 180rem * $unitSize;
      border-radius: 50%;
      border: 2px dashed red;
      box-sizing: border-box;
      position: relative;
      overflow: hidden;
      margin-bottom: 30px;
      video,
      canvas {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .imgSrc{
      width: 200px;
      height: auto;
    }
  }
}
</style>



